<template>
  <div ref="wrap" class="tbl-wrap tbl-wrap-id-2018-03-26">

    <span ref="textLengtn" style="display: inline-block; position: absolute; z-index: -99; font-size: 13px; background: transparent"></span>

    <!-- 滚动区域 -->
    <div 
        ref="container" 
        class="container" 
        @mousewheel="mousewheel"
        @touchstart="touchstart"
        :style="{
            width: width + 'px',
            height: height + 'px',
        }">
        <div class="body">
            <!-- //专门撑出滚动条的DOM// -->
            <div 
                class="expand" 
                :style="{
                    width: fullWidth + 'px',
                    height: fullHeight + 'px',
                }">
            </div>

            <!-- //表头// -->
            <div class="sheet-header">

              <!-- 表头-左侧固定 -->
              <div class="sheet-header-left">
                
                <!-- 表头-复选框 -->
                <div 
                  v-if="showCheckBox"
                  class="tbl-cell check-cell" 
                  :style="{
                    height: cellHeight + 'px',
                    left: 0 + 'px',
                    width: 40 + 'px',
                  }"
                  >
                  <!-- 没有行 -->
                  <i key="1" v-show="headerCheckStatus == false" class="fa fa-square-o" style="color: #999"></i>
                  <!-- 全选中 -->
                  <i key="2" v-show="headerCheckStatus == 'all'" @click="headerCheckStatus = 'none'" class="fa fa-check-square"></i>
                  <!-- 未选择 -->
                  <i key="3" v-show="headerCheckStatus == 'none'" @click="headerCheckStatus = 'all'" class="fa fa-square-o"></i>
                  <!-- 部分选择 -->
                  <i key="4" v-show="headerCheckStatus == 'some'" @click="headerCheckStatus = 'all'" class="fa fa-square"></i>
                </div>

                <div
                  v-for="colFixHeaderItem in colFixHeader"
                  :key="colFixHeaderItem._colid"
                  class="tbl-cell nowrap" 
                  :style="{
                      height: cellHeight + 'px',
                      left: colFixHeaderItem._left + 'px',
                      width: colFixHeaderItem.width + 'px'
                  }">
                  <template v-if="colFixHeaderItem.icon">
                    <i v-if="colFixHeaderItem.icon == 'mul_select'" class="icon fa fa-chevron-circle-down"></i>
                    <i v-else-if="colFixHeaderItem.icon == 'select'" class="icon fa fa-angle-down"></i>
                    <i v-else-if="colFixHeaderItem.icon == 'normal'" class="icon fa fa-info-circle"></i>
                    <i v-else-if="colFixHeaderItem.icon == 'edit'" class="icon fa fa-pencil-square-o"></i>
                    <i v-else-if="colFixHeaderItem.icon == 'number'" class="icon fa fa-sort"></i>
                    <i v-else-if="colFixHeaderItem.icon == 'date'" class="icon fa fa-calendar-o"></i>
                    <i v-else-if="colFixHeaderItem.icon == 'time'" class="icon fa fa-clock-o"></i>
                    <i v-else :class="[colFixHeaderItem.icon, 'icon']"></i>
                  </template>&nbsp;
                  {{colFixHeaderItem.title}}
                  <span @mousedown.prevent.stop="dragDown(colFixHeaderItem, $event)" class="drag-pad">&nbsp;</span>
                </div>
              </div>

              <!-- 表头-右侧活动列 -->
              <div 
                class="sheet-header-right" 
                :style="{
                  left: colFixHeaderWidth + 'px',
                  transform: 'translate('+ -1*geScrollLeftArea +'px, 0px)'
                }">
                <!-- (x轴方向的动态渲染) -->
                <div 
                  v-if="
                    geScrollLeftArea - (colActiveHeaderItem._left - colFixHeaderWidth) <=  colActiveHeaderItem.width
                    &&
                    (colActiveHeaderItem._left - colFixHeaderWidth) - geScrollLeftArea - width <= colActiveHeaderItem.width
                  "
                  v-for="colActiveHeaderItem in colActiveHeader"
                  :key="colActiveHeaderItem._colid"
                  class="tbl-cell nowrap" 
                  :style="{
                      height: cellHeight + 'px',
                      left: colActiveHeaderItem._left - colFixHeaderWidth + 'px',
                      width: colActiveHeaderItem.width + 'px',
                  }">
                  <template v-if="colActiveHeaderItem.icon">
                    <i v-if="colActiveHeaderItem.icon == 'mul_select'" class="icon fa fa-chevron-circle-down"></i>
                    <i v-else-if="colActiveHeaderItem.icon == 'select'" class="icon fa fa-angle-down"></i>
                    <i v-else-if="colActiveHeaderItem.icon == 'normal'" class="icon fa fa-info-circle"></i>
                    <i v-else-if="colActiveHeaderItem.icon == 'edit'" class="icon fa fa-pencil-square-o"></i>
                    <i v-else-if="colActiveHeaderItem.icon == 'number'" class="icon fa fa-sort"></i>
                    <i v-else-if="colActiveHeaderItem.icon == 'date'" class="icon fa fa-calendar-o"></i>
                    <i v-else-if="colActiveHeaderItem.icon == 'time'" class="icon fa fa-clock-o"></i>
                    <i v-else :class="[colActiveHeaderItem.icon, 'icon']"></i>
                  </template>&nbsp;
                  {{colActiveHeaderItem.title}}
                  <span @mousedown.prevent.stop="dragDown(colActiveHeaderItem, $event)" class="drag-pad">&nbsp;</span>
                </div>
              </div>

            </div>

            <!-- //表体// -->
            <div 
                class="sheet-body"
                :style="{
                    transform: 'translate(0px, '+ -1*geScrollTopArea +'px)'
                }">

                <!-- (y轴方向的动态渲染) -->
                <div 
                  class="sheet-line"
                  v-if="
                    (rowLine + 1) * cellHeight - geScrollTopArea > -cellHeight
                    &&
                    (rowLine + 1) * cellHeight - geScrollTopArea < height + cellHeight
                  "
                  v-for="(rowItem, rowLine) in row"
                  :key="rowItem._rowid + '-row'"
                  :style="{
                    top: (rowLine + 1) * cellHeight + 'px',
                  }">

                  <!-- 表体-左侧固定 -->
                  <div class="sheet-body-left">
                    <!-- 表体-复选框 -->
                    <div 
                      v-if="showCheckBox"
                      class="tbl-cell check-cell" 
                      :class="[rowItem._modify? rowItem._error? 'error': 'modify': '']"
                      :style="{
                        height: cellHeight + 'px',
                        left: 0 + 'px',
                        width: 40 + 'px',
                      }"
                      @mouseover.stop="$set(lineHover, 'lineHover-'+rowItem._rowid, true)"
                      @mouseout.stop="$set(lineHover, 'lineHover-'+rowItem._rowid, false)"
                      >
                      <template v-if="lineHover['lineHover-'+rowItem._rowid] || rowItem.checked">
                        <i @click="checkBox(rowItem)" v-show="rowItem.checked" class="fa fa-check-square"></i>
                        <i @click="checkBox(rowItem)" v-show="!rowItem.checked" class="fa fa-square-o"></i>
                      </template>
                      <template v-else>
                        <span class="nowrap" style="font-size: 10px">{{rowLine*1+1}}</span>
                      </template>
                    </div>

                    <div
                      class="tbl-cell" 
                      v-for="col in colFixHeader" 
                      :key="rowItem._rowid + '-' + col._colid"
                      :style="{
                          left: col._left + 'px',
                          width: col.width + 'px',
                          height: cellHeight + 'px'
                      }"
                      :ref="'cell-'+rowItem._rowid+'-'+col._colid"
                      @mouseover.stop="cellOver(rowItem, col)"
                      @mouseout="cellOut(rowItem, col)"
                      >
                      
                      <!-- 普通框 -->
                      <div 
                        class="cell"  
                        v-if="rowItem[col.props].type == 'normal'"
                        :ref="'cell-'+rowItem._rowid+'-'+col._colid"
                        >
                        <div class="text nowrap">
                            {{rowItem[col.props].value}}
                        </div>
                      </div>

                      <!-- 编辑框 -->
                      <div 
                        :ref="'cell-'+rowItem._rowid+'-'+col._colid" 
                        class="cell" 
                        v-if="rowItem[col.props].type == 'edit'">
                        <input 
                          type="text" 
                          :value="rowItem[col.props].value" 
                          class="edit-inp nowrap"
                          @focus="option_show = false;"
                          @input="
                            modifyByRowCol(
                              rowItem._rowid, 
                              col.props, 
                              $event.target.value
                            )
                          "
                        />
                        <i class="fa fa-pencil-square-o edit-close"></i>
                      </div>

                      <!-- 数字输入 -->
                      <div 
                        :ref="'cell-'+rowItem._rowid+'-'+col._colid" 
                        class="cell" 
                        v-if="rowItem[col.props].type == 'number'">
                        <input 
                          type="number" 
                          :value="rowItem[col.props].value" 
                          class="edit-inp nowrap"
                          @focus="option_show = false;"
                          @input="
                            modifyByRowCol(
                              rowItem._rowid, 
                              col.props, 
                              $event.target.value
                            );
                          "
                          @blur=" checkNumber(rowItem, col, $event.target.value); "
                        />
                      </div>

                      <!-- 单选框 -->
                      <div 
                        :ref="'cell-'+rowItem._rowid+'-'+col._colid"
                        class="cell"  
                        @click.stop
                        v-if="rowItem[col.props].type == 'select'">
                        <input 
                          type="search" 
                          :value="rowItem[col.props].value" 
                          class="edit-inp nowrap"
                          readonly
                          @focus="searchFocus(rowItem, col)"
                          @blur.stop
                        />
                        <i v-show="(rowItem[col.props].id+'').trim() != ''" @click="selectClose(rowItem, col, 'select')" class="fa fa-times-circle edit-close"></i>
                      </div>

                      <!-- 多选框 -->
                      <div 
                        :ref="'cell-'+rowItem._rowid+'-'+col._colid"
                        class="cell"  
                        @click.stop
                        v-if="rowItem[col.props].type == 'mul_select'">
                        <input 
                          type="search"
                          :value="rowItem[col.props].value.join('， ')" 
                          class="edit-inp nowrap"
                          readonly
                          @focus="searchFocus(rowItem, col, true)"
                          @blur.stop
                        />
                        <i v-show="rowItem[col.props].id.length != 0" @click="selectClose(rowItem, col, 'mul_select')" class="fa fa-times-circle edit-close"></i>
                      </div>

                      <!-- 日期、时间、日期时间、月份、年份 -->
                      <div 
                        :ref="'cell-'+rowItem._rowid+'-'+col._colid" 
                        class="cell" 
                        v-if="
                        rowItem[col.props].type == 'date' ||
                        rowItem[col.props].type == 'time' ||
                        rowItem[col.props].type == 'datetime' ||
                        rowItem[col.props].type == 'month' ||
                        rowItem[col.props].type == 'year'
                        ">
                        <input  
                          type="text" 
                          :value="rowItem[col.props].value" 
                          class="edit-inp nowrap"
                          :placeholder="placeholder_date(rowItem[col.props].type)"
                          @focus="option_show = false;"
                          @input="
                            modifyByRowCol(
                              rowItem._rowid, 
                              col.props, 
                              $event.target.value
                            );
                          "
                          @blur=" checkDate(rowItem, col, $event.target.value, rowItem[col.props].type); "
                        />
                        <i :class="[rowItem[col.props].type == 'time'? 'fa-clock-o': 'fa-calendar-o']" class="fa edit-close"></i>
                      </div>

                    </div>
                  </div>

                  <!-- 表体-右侧活动 -->
                  <div 
                    class="sheet-body-right"
                    :style="{
                        left: colFixHeaderWidth + 'px',
                        transform: 'translate('+ -1*geScrollLeftArea +'px, 0px)'
                    }">
                    <!-- (x轴方向的动态渲染) -->
                    <div
                      v-if="
                        geScrollLeftArea - col._left <  col.width
                        &&
                        col._left - geScrollLeftArea - width < col.width
                      "
                      class="tbl-cell" 
                      v-for="col in colActiveHeader" 
                      :key="rowItem._rowid + '-' + col._colid"
                      :style="{
                          left: col._left - colFixHeaderWidth + 'px',
                          width: col.width + 'px',
                          height: cellHeight + 'px'
                      }"
                      :ref="'cell-'+rowItem._rowid+'-'+col._colid"
                      @mouseover.stop="cellOver(rowItem, col)"
                      @mouseout="cellOut(rowItem, col)"
                      >
                      
                      <!-- 普通框 -->
                      <div 
                        class="cell"  
                        v-if="rowItem[col.props].type == 'normal'"
                        :ref="'cell-'+rowItem._rowid+'-'+col._colid"
                        >
                        <div class="text nowrap">
                            {{rowItem[col.props].value}}
                        </div>
                      </div>

                      <!-- 编辑框 -->
                      <div 
                        :ref="'cell-'+rowItem._rowid+'-'+col._colid" 
                        class="cell" 
                        v-if="rowItem[col.props].type == 'edit'">
                        <input 
                          type="text" 
                          :value="rowItem[col.props].value" 
                          class="edit-inp nowrap"
                          @focus="option_show = false;"
                          @input="
                            modifyByRowCol(
                              rowItem._rowid, 
                              col.props, 
                              $event.target.value
                            )
                          "
                        />
                        <i class="fa fa-pencil-square-o edit-close"></i>
                      </div>

                      <!-- 数字输入 -->
                      <div 
                        :ref="'cell-'+rowItem._rowid+'-'+col._colid" 
                        class="cell" 
                        v-if="rowItem[col.props].type == 'number'">
                        <input 
                          type="number" 
                          :value="rowItem[col.props].value" 
                          class="edit-inp nowrap"
                          @focus="option_show = false;"
                          @input="
                            modifyByRowCol(
                              rowItem._rowid, 
                              col.props, 
                              $event.target.value
                            );
                          "
                          @blur=" checkNumber(rowItem, col, $event.target.value); "
                        />
                      </div>

                      <!-- 单选框 -->
                      <div 
                        :ref="'cell-'+rowItem._rowid+'-'+col._colid"
                        class="cell"  
                        @click.stop
                        v-if="rowItem[col.props].type == 'select'">
                        <input 
                          type="search" 
                          :value="rowItem[col.props].value" 
                          class="edit-inp nowrap"
                          readonly
                          @focus="searchFocus(rowItem, col)"
                          @blur.stop
                        />
                        <i v-show="(rowItem[col.props].id+'').trim() != ''" @click="selectClose(rowItem, col, 'select')" class="fa fa-times-circle edit-close"></i>
                      </div>

                      <!-- 多选框 -->
                      <div 
                        :ref="'cell-'+rowItem._rowid+'-'+col._colid"
                        class="cell"  
                        @click.stop
                        v-if="rowItem[col.props].type == 'mul_select'">
                        <input 
                          type="search"
                          :value="rowItem[col.props].value.join('， ')" 
                          class="edit-inp nowrap"
                          readonly
                          @focus="searchFocus(rowItem, col, true)"
                          @blur.stop
                        />
                        <i v-show="rowItem[col.props].id.length != 0" @click="selectClose(rowItem, col, 'mul_select')" class="fa fa-times-circle edit-close"></i>
                      </div>

                      <!-- 日期、时间、日期时间、月份、年份 -->
                      <div 
                        :ref="'cell-'+rowItem._rowid+'-'+col._colid" 
                        class="cell" 
                        v-if="
                        rowItem[col.props].type == 'date' ||
                        rowItem[col.props].type == 'time' ||
                        rowItem[col.props].type == 'datetime' ||
                        rowItem[col.props].type == 'month' ||
                        rowItem[col.props].type == 'year'
                        ">
                        <input  
                          type="text" 
                          :value="rowItem[col.props].value" 
                          class="edit-inp nowrap"
                          :placeholder="placeholder_date(rowItem[col.props].type)"
                          @focus="option_show = false;"
                          @input="
                            modifyByRowCol(
                              rowItem._rowid, 
                              col.props, 
                              $event.target.value
                            );
                          "
                          @blur=" checkDate(rowItem, col, $event.target.value, rowItem[col.props].type); "
                        />
                        <i :class="[rowItem[col.props].type == 'time'? 'fa-clock-o': 'fa-calendar-o']" class="fa edit-close"></i>
                      </div>

                    </div>
                  </div>

                </div>

            </div>

        </div>

        <!-- x轴-滚动条 -->
        <div class="scrollbar-x" :style="{width: width+'px'}">
            <div 
                @mousedown.prevent.stop="scrollMouseDown($event, 'x')"
                class="scrollbar-x-drag" 
                :style="{
                    width: width * width / fullWidth + 'px',
                    left: scrollLeft + 'px'
                }">
            </div>
        </div>

        <!-- y轴-滚动条 -->
        <div class="scrollbar-y" :style="{height: height+'px'}">
            <div 
                @mousedown.prevent.stop="scrollMouseDown($event, 'y')"
                class="scrollbar-y-drag" 
                :style="{
                    height: height * height / fullHeight + 'px',
                    top: scrollTop + 'px'
                }">
            </div>
        </div>
        
        <div v-show="dragLineShow" :style="{left: dragLeft + 'px'}" class="drag_line"></div>

    </div>

    <!-- option的DOM -->
    <transition name="option">
      <div 
        class="option"
        ref="optionDOM"
        @click.stop
        @mousewheel.stop
        @mouseover.stop
        v-show="option_show"
        :style="{
          left: option_left + 'px',
          top: option_top + 'px',
          width: option_width + 'px',
        }"
        >
        <div class="arrow" v-show="!option_hide_arrow"></div>
        <div class="opp">
          <div style="text-align: center;" v-if="option_loading">加载中...</div>
          <div v-else>
            <div 
              class="option-item"
              v-for="d in option_data"
              :class="[d.select? 'select': '']"
              @click="optionItemClick(d)"
              :key="d.id"
              >
              {{d.name}}
            </div>
          </div>
        </div>
      </div>
    </transition>

    <!-- hover提示框 -->
    <div
      v-if="showPrompt"
      v-show="hover_show"
      class="hover-show"
      :style="{
        left: hover_left - 6 + 'px',
        bottom: hover_bottom + 'px',
        width: hover_width + 12 + 'px',
      }">
      <span v-html="hover_text"></span>
    </div>

  </div>
</template>

<script>

export default {
  props: {
    // 列数据
    colData: { required: true },
    // 行数据
    rowData: { required: true },

    // 增加行的数据格式
    entry:  { default: false },
    // 滚动条-速度
    scrollSpeed: { default: 15 },
    // 是否出现复选框
    showCheckBox: { default: true },
    // 是否出现提示框
    showPrompt: { default: true }

  },
  data() {
    return {
      // 容器宽度
      width: 0,
      // 容器高度
      height: 0,
      // 单元格高度
      cellHeight: 40,
      // 滚动条的left距离
      scrollLeft: 0,
      // 滚动条的top距离
      scrollTop: 0,
      //每一行的hover存储
      lineHover: {},
      //列数据
      col: [],
      //行数据
      row: [],
      /**
       * 下拉框变量集合
       */
      option_data: [], //option数据
      option_info: {}, //option其他信息
      option_loading: false, //option加载状态
      option_width: 0, //option宽度
      option_hide_arrow: false, //是否隐藏option小箭头
      option_left: 0, //option的left
      option_top: 0, //option的top
      option_show: false, //option是否显示
      /**
      * 拖拽线变量
      */
     dragLeft: 0,
     dragLineShow: false,

     //hover单元格的变量
     hover_show: false,
     hover_info: {},
     hover_width: 0,
     hover_bottom: 0,
     hover_left: 0,

    };
  },

  computed: {
    //总宽度
    fullWidth() {
      let w = 0;
      for (let i = 0; i < this.col.length; i++) {
        w += this.col[i].width;
      }
      if(this.showCheckBox){
        w += 40;
      }
      return w;
    },
    //总高度
    fullHeight() {
      let needHeightNumber = 1; //因为表头占了top=0；所以最后一行会不见，需要多加一行计算！
      return this.cellHeight * (this.row.length + needHeightNumber);
    },
    //x轴的偏移距离
    geScrollLeftArea: {
      get: function() {
        //x轴-滚动条宽度
        let x_width = this.width * this.width / this.fullWidth;
        //需要平移的宽度
        let tranlateX = 0;
        tranlateX =
          (this.scrollLeft + x_width) / this.width * this.fullWidth -
          this.width;
        return tranlateX;
      },
      set: function(val) {
        let x_width = this.width * this.width / this.fullWidth;
        this.scrollLeft =
          (val + this.width) / this.fullWidth * this.width - x_width;
      }
    },
    //y轴的偏移距离
    geScrollTopArea: {
      get: function() {
        //y轴-滚动条宽度
        let y_width = this.height * this.height / this.fullHeight;
        //返回需要平移的高度
        let tranlateY = 0;
        tranlateY =
          (this.scrollTop + y_width) / this.height * this.fullHeight -
          this.height;
        return tranlateY;
      },
      set: function(val) {
        let y_width = this.height * this.height / this.fullHeight;
        this.scrollTop =
          (val + this.height) / this.fullHeight * this.height - y_width;
      }
    },
    //表头-左侧固定列
    colFixHeader() {
      return this.col.filter(col => col.fixed);
    },
    //表头-右侧活动列
    colActiveHeader() {
      return this.col.filter(col => !col.fixed);
    },
    //表头-左侧固定列【总宽度】
    colFixHeaderWidth() {
      let w = 0;
      for (let i = 0; i < this.colFixHeader.length; i++) {
        w += this.colFixHeader[i].width;
      }
      return w;
    },
    //表头-复选框的状态
    headerCheckStatus: {
      get: function(){
        let all_len = this.row.length;
        let checked_len = this.row.filter(row => row.checked).length;
        //没有行数
        if(all_len == 0){
          return false;
        }
        //全部选中
        if(all_len == checked_len){
          return 'all';
        }
        //部分选中
        if(checked_len > 0){
          return 'some';
        }
        //无选中
        if(checked_len == 0){
          return 'none';
        }
      },
      set: function(val){
        if(val == 'none'){
          let row = this.deepClone(this.row);
          row.forEach(r=>{
            r.checked = false
          });
          this.row = row;
        }
        if(val == 'all'){
          let row = this.deepClone(this.row);
          row.forEach(r=>{
            r.checked = true
          });
          this.row = row;
        };
      }
    },
    //表头-复选框被选中的行
    checkedRows(){
      return this.row.filter(rr => rr.checked);
    },
    //获取表格信息
    getTable(){
      return {row: this.row, col: this.col};
    },
    //hover的提示效果
    hover_text(){
      if(this.hover_info.type != 'mul_select'){
        return this.hover_info.value;
      };
      let res = this.row.filter(r=>r._rowid == this.hover_info.rowid);
      if(res.length == 0) return ''
      let textArr = res[0][this.hover_info.props].value;
      let html = `${ textArr.map( text => ` <span style="display: inline-block; margin: 2px; background: #e7f3ff; color: #009fff; padding: 2px 6px; border: 1px solid #98def2;">${text}</span> ` ).join('') }`
      return html;
    },
  },

  created() {
    /**
    * 整体初始化
    */
    this.init();
  },

  watch: {
    
  },

  mounted() {
    this.fixedWidthHeight();
    window.addEventListener("resize", this.fixedWidthHeight);
    document.addEventListener("click", this.hideOption);
  },

  methods: {
    console(m){
      window.console.log(m)
    },
    // 初始化
    init() {
      this.row = this.deepClone(this.rowData);
      this.col = this.deepClone(this.colData);
      /**
       * @name 定义行数据、列数据的_rowid、_colid
       */
      let rowid = 0;
      let colid = 0;
      for (let i = 0; i < this.row.length; i++) {
        this.row[i]._rowid = rowid;
        this.row[i]._error = false;
        this.row[i]._modify = false;
        rowid++;
      }
      for (let i = 0; i < this.col.length; i++) {
        this.col[i]._colid = colid;
        colid++;
      }

      /**
       * @name 定义每列离最左侧的绝对距离
       */
      for (let i = 0; i < this.col.length; i++) {
        let left = 0;
        if(this.showCheckBox){
          left = 40;
        };
        for (let j = 0; j < i; j++) {
          left += this.col[j].width;
        }
        this.col[i]._left = left;
      }

    },
    // 修复容器的高度、宽度
    fixedWidthHeight() {
      let wrap = this.$refs.wrap;
      if (wrap instanceof Array) {
        wrap = wrap[0];
      }
      this.width = wrap.offsetWidth;
      this.height = wrap.offsetHeight;
    },
    // 日期时间的placeholder
    placeholder_date(type){
      if(type == 'date'){
        return 'yyyy-MM-dd';
      }
      if(type == 'time'){
        return 'hh:mm:ss';
      }
      if(type == 'datetime'){
        return 'yyyy-MM-dd hh:mm:ss';
      }
      if(type == 'month'){
        return '输入月份';
      }
      if(type == 'year'){
        return '输入年份';
      }   
    },
    // 表体-复选框的切换
    checkBox(row){
      for (let i = 0; i < this.row.length; i++) {
        if (this.row[i]._rowid == row._rowid) {
          this.row[i].checked = !this.row[i].checked
          return;
        }
      };
    },
    // 滚动条-拖拽按下
    scrollMouseDown(e, type) {
      e.preventDefault();
      this.option_show = false;
      let vm = this;
      let left, offsetX, top, offsetY;
      if (type == "x") {
        left = this.scrollLeft;
        offsetX = e.clientX;
      } else if (type == "y") {
        top = this.scrollTop;
        offsetY = e.clientY;
      } else {
        console.warn("请传入参数type");
        return false;
      }
      window.addEventListener("mousemove", mousemove);
      window.addEventListener("mouseup", mouseup);
      function mousemove(e) {
        if (type == "x") {
          let ll = left + (e.clientX - offsetX);
          if (ll < 0) ll = 0;
          let drag_w = vm.width * vm.width / vm.fullWidth;
          if (ll + drag_w > vm.width) ll = vm.width - drag_w;
          vm.scrollLeft = ll;
        } else {
          let tt = top + (e.clientY - offsetY);
          if (tt < 0) tt = 0;
          let drag_h = vm.height * vm.height / vm.fullHeight;
          if (tt + drag_h > vm.height) tt = vm.height - drag_h;
          vm.scrollTop = tt;
        }
      }
      function mouseup(e) {
        window.removeEventListener("mousemove", mousemove);
        window.removeEventListener("mouseup", mouseup);
      }
    },
    // 滚动条-鼠标滚轮
    mousewheel(e) {
      this.option_show = false;
      let maxTop = this.height - this.height * this.height / this.fullHeight;
      let scrollTop = this.scrollTop;
      //滚轮速度
      let scrollYSpeed = this.scrollSpeed;
      if (e.wheelDelta) {
        // IE/谷歌
        if (e.wheelDelta > 0) {
          scrollTop -= scrollYSpeed;
        }
        if (e.wheelDelta < 0) {
          scrollTop += scrollYSpeed;
        }
      } else if (e.detail) {
        // 火狐
        if (e.detail > 0) {
          scrollTop -= scrollYSpeed;
        }
        if (e.detail < 0) {
          scrollTop += scrollYSpeed;
        }
      }
      if (scrollTop < 0) scrollTop = 0;
      if (scrollTop > maxTop) scrollTop = maxTop;
      this.scrollTop = scrollTop;
    },
    // 触摸开始
    touchstart(e) {
      e.preventDefault();
      let block = this.$refs.container;
      let touches = e.touches[0];
      this._geScrollLeftArea = this.geScrollLeftArea;
      this._geScrollTopArea = this.geScrollTopArea;
      this._startx = Math.floor(touches.pageX);
      this._starty = Math.floor(touches.pageY);
      block.addEventListener("touchmove", this.touchmove);
      block.addEventListener("touchend", this.touchend);
    },
    // 触摸移动
    touchmove(e) {
      e.preventDefault();
      let endx = Math.floor(e.changedTouches[0].pageX);
      let endy = Math.floor(e.changedTouches[0].pageY);
      let nx = endx - this._startx;
      let ny = endy - this._starty;
      let angle = Math.atan2(ny, nx) * 180 / Math.PI;
      let geScrollTopArea = 0;
      let geScrollLeftArea = 0;
      if (Math.abs(nx) <= 1 || Math.abs(ny) <= 1) {
        // 滑动距离太小
        return false;
      }
      if (angle < 45 && angle >= -45) {
        //右滑动
        geScrollLeftArea = this._geScrollLeftArea - Math.abs(nx);
        if (geScrollLeftArea < 0) geScrollLeftArea = 0;
        this.geScrollLeftArea = geScrollLeftArea;
      } else if (angle < 135 && angle >= 45) {
        //下滑动
        geScrollTopArea = this._geScrollTopArea - Math.abs(ny);
        if (geScrollTopArea < 0) geScrollTopArea = 0;
        this.geScrollTopArea = geScrollTopArea;
      } else if (
        (angle <= 180 && angle >= 135) ||
        (angle >= -180 && angle < -135)
      ) {
        //左滑动
        geScrollLeftArea = this._geScrollLeftArea + Math.abs(nx);
        if (geScrollLeftArea > this.fullWidth - this.width)
          geScrollLeftArea = this.fullWidth - this.width;
        this.geScrollLeftArea = geScrollLeftArea;
      } else if (angle <= -45 && angle >= -135) {
        //上滑动
        geScrollTopArea = this._geScrollTopArea + Math.abs(ny);
        if (geScrollTopArea > this.fullHeight - this.height)
          geScrollTopArea = this.fullHeight - this.height;
        this.geScrollTopArea = geScrollTopArea;
      }
    },
    // 触摸结束
    touchend(e) {
      e.preventDefault();
      let block = this.$refs.container;
      block.removeEventListener("touchmove", this.touchmove);
      block.removeEventListener("touchup", this.touchup);
    },

    /**
    * @description 修改this.row对应项
    */
    modifyByRowCol(rowid, props, val, id) {
      for (let i = 0; i < this.row.length; i++) {
        if (this.row[i]._rowid == rowid) {
          this.row[i][props].value = val;
          if (id != undefined) {
            this.row[i][props].id = id;
            //下拉才会改变id，所以在这可以派发下拉选中
            let col = null;
            for(let j=0; j<this.col.length; j++){
              if(this.col[j].props == props){
                col = this.col[j]
                break ;
              }
            };
            this.$emit('selectChange', {value: val, id, rowid, col});
          }
          //该行被修改过了
          this.row[i]._modify = true;
          //重置错误标记
          this.row[i]._error = false;
          return;
        }
      }
    },
    //数字框-验证
    checkNumber(row, col, val){
      val *= 1;
      if(row[col.props].min != undefined){
        if(val < row[col.props].min){
          val = row[col.props].min;
        }
      }
      if(row[col.props].max != undefined){
        if(val > row[col.props].max){
          val = row[col.props].max;
        }
      }
      if(row[col.props].floatLength != undefined){
        val = (val*1).toFixed(row[col.props].floatLength)
      }
      this.modifyByRowCol(row._rowid, col.props, val);
    },
    //日期、时间、日期时间、年、月-验证
    checkDate(row, col, val, type){
      // 日期 正则
      let dateReg = /^[1-9]\d{3}-(0[1-9]|1[0-2])-(0[1-9]|[1-2][0-9]|3[0-1])$/;
      // 时间 正则
      let timeReg = /^(20|21|22|23|[0-1]\d):[0-5]\d:[0-5]\d$/;
      // 日期+时间 正则
      let dateTimeReg = /^[1-9]\d{3}-(0[1-9]|1[0-2])-(0[1-9]|[1-2][0-9]|3[0-1])\s+(20|21|22|23|[0-1]\d):[0-5]\d:[0-5]\d$/;
      // 年份-正则
      let yearReg = /^(1849|18[5-9]\d|20\d{2}|2200)$/;
      // 月份-正则
      let monthReg = /^0?[1-9]$|^1[0-2]$/;

      if((val+'').trim() == ''){
        this.modifyByRowCol(row._rowid, col.props, '');
        return ; 
      }
      if(type == 'date'){
        let regExp = new RegExp(dateReg);
        if(!regExp.test(val)){
          this.modifyByRowCol(row._rowid, col.props, '');
          return ;
        }
      }
      if(type == 'time'){
        let regExp = new RegExp(timeReg);
        if(!regExp.test(val)){
          this.modifyByRowCol(row._rowid, col.props, '');
          return ;
        }
      }
      if(type == 'datetime'){
        let regExp = new RegExp(dateTimeReg);
        if(!regExp.test(val)){
          this.modifyByRowCol(row._rowid, col.props, '');
          return ;
        }
      }
      if(type == 'month'){
        let regExp = new RegExp(monthReg);
        if(!regExp.test(val)){
          this.modifyByRowCol(row._rowid, col.props, '');
          return ;
        }
      }
      if(type == 'year'){
        let regExp = new RegExp(yearReg);
        if(!regExp.test(val)){
          this.modifyByRowCol(row._rowid, col.props, '');
          return ;
        }
      }
      this.modifyByRowCol(row._rowid, col.props, val);
    },
    //下拉input的close
    selectClose(row, col, type){
      if(type == 'mul_select'){
        this.modifyByRowCol(row._rowid, col.props, [], []);
      }else if(type == 'select'){
        this.modifyByRowCol(row._rowid, col.props, '', '');
      };
      this.hideOption();
    },

    //单选下拉-（聚焦+远程搜索）
    searchFocus(row, col, isMul){
      let dom = this.$refs['cell-'+row._rowid+'-'+col._colid];
      if(dom instanceof Array){
        dom = dom[0];
      };
      //先全部选中
      dom.querySelector('input').select();

      let rect = dom.getBoundingClientRect();
      let rect2 = this.$refs.container.getBoundingClientRect();

      //option的处理
      this.option_info = {row, col};
      this.option_left = rect.left - rect2.left - 5;
      this.option_top = rect.top - rect2.top + rect.height + 8;
      this.option_hide_arrow = false;
      this.option_width = Math.max(rect.width, 166);
      this.option_loading = true;
      this.option_show = true;

      //兼容火狐的滚轮事件
      this._wheelStop = function(e){
        e.stopPropagation();
      }
      this.$refs.optionDOM.removeEventListener('DOMMouseScroll', this._wheelStop)
      this.$refs.optionDOM.addEventListener('DOMMouseScroll', this._wheelStop)

      //通过http请求option数据
      this.$nextTick(()=>{
        let pos = row[col.props].dataPos || 'data.result';
        pos = pos.split('.');
        this.searchRemote(row[col.props]).then(res=>{
          let realData = res.data;
          for(let i=0; i<pos.length; i++){
            realData = realData[pos[i]]
          };
          this.option_loading = false;
          this.cellOver(row, col, true)
          //对选中的select状态进行处理
          if(isMul){
            for(let i=0; i<realData.length; i++){
              for(let j=0; j<row[col.props].id.length; j++){
                if(realData[i].id == row[col.props].id[j]){
                  realData[i].select = true;
                }
              }
            }
          }else{
            for(let i=0; i<realData.length; i++){
              if(realData[i].id == row[col.props].id){
                realData[i].select = true;
                break ;
              }
            }
          };
          this.option_data = realData;
        }).catch(err=>{
          this.option_loading = false;
          this.option_data = [];
          console.error(err);
        });
      });
    },
    //option下拉-选择某项
    optionItemClick(d){
      let type = this.option_info.row[this.option_info.col.props].type;
      
      if(type == 'mul_select'){
        let old_id = this.jsonClone(this.option_info.row[this.option_info.col.props].id);
        let old_name = this.jsonClone(this.option_info.row[this.option_info.col.props].value);
        let old_option_data = this.jsonClone(this.option_data).map(o=>{
          o.select = false; 
          return o;
        });
        // 判断点击项的id、name是否存在
        let have_id = false;
        for(let i=0; i<old_id.length; i++){
          if(old_id[i] == d.id){
            old_id.splice(i, 1);
            have_id = true;
            break ;
          };
        };
        if(!have_id){
          old_id.push(d.id);
        };

        let have_name = false;
        for(let i=0; i<old_name.length; i++){
          if(old_name[i] == d.name){
            old_name.splice(i, 1);
            have_name = true;
            break ;
          };
        };
        if(!have_name){
          old_name.push(d.name);
        };

        //更新select状态
        for(let i=0; i<old_option_data.length; i++){
          for(let j=0; j<old_id.length; j++){
            if(old_option_data[i].id == old_id[j]){
              old_option_data[i].select = true;
            }
          }
        };

        this.option_data = old_option_data;
        this.$nextTick(()=>{
          this.modifyByRowCol(
            this.option_info.row._rowid,
            this.option_info.col.props,
            old_name,
            old_id
          );
        })
        
      }else{
        this.modifyByRowCol(
          this.option_info.row._rowid,
          this.option_info.col.props,
          d.name,
          d.id
        );
        this.option_show = false;
      };
    },
    //option远程搜索
    searchRemote(row){
      if(!row.url){
        return Promise.reject('缺失url！');
      }
      return this.$http.get(row.url);
    },
    //隐藏option
    hideOption(){
      this.hover_show = false;
      this.option_show = false;
    },
    //增加一行
    addLine(){
      if(this.entry != false && JSON.stringify(this.entry) != '{}'){
        let entry = this.deepClone(this.entry);
        entry.checked = false;
        entry._error = false;
        entry._modify = true;
        entry._rowid = Date.now();
        for(let [key, value] of Object.entries(entry)){
          //value字段是：function
          if(typeof value.value == 'function'){
            entry[key].value = value.value()
          }
          //id字段是：function
          if(typeof value.id == 'function'){
            entry[key].id = value.id()
          }
        };
        this.row.unshift(entry);
        //增加一行后，回到顶部
        this.$nextTick(()=>{
          this.scrollTop = 0;
        });
      }
    },
    //调整布局
    doLayout(){
      this.fixedWidthHeight();
    },
    //单元格移入提示框
    cellOver(row, col, byFocus){
      if(!byFocus){
        //多选下拉聚焦时再显示
        if(this.option_show) return ;
        this.hover_show = false;
      };
      let val = row[col.props].value;
      //无值不显示提示框
      if((val+'').trim() == ''){
        this.hover_show = false;
        return ;
      };
      //长度未超过宽度也不用显示提示框
      let txt = this.$refs.textLengtn;
      txt.innerText = val;
      if(txt.offsetWidth*1 + 20 < col.width && row[col.props].type != 'mul_select'){
        this.hover_show = false;
        return ;
      }
      
      let hoverCell = this.$refs['cell-'+row._rowid+'-'+col._colid];
      let container = this.$refs['container'];
      if(hoverCell instanceof Array){
        hoverCell = hoverCell[0];
      };
      this.hover_info = {
        props: col.props, 
        rowid: row._rowid, 
        type: row[col.props].type,
        value: row[col.props].value,
      }
      let rect1 = hoverCell.getBoundingClientRect();
      let rect2 = container.getBoundingClientRect();
      let w = 0;
      if(row[col.props].type == 'mul_select'){
        w = Math.max(col.width, 214);
      }else{
        w = Math.max(col.width, 134);
      };
      let dis_w = w - col.width;
      this.hover_width = w;
      this.hover_left = rect1.left - rect2.left - dis_w/2;
      this.hover_bottom = this.height - (rect1.top - rect2.top) + 10;
      
      this.$nextTick(()=>{
        this.hover_show = true;
      });

    },
    cellOut(row, col){
      if(this.option_show) return ;
      this.hover_show = false;
    },
    //检查required
    checkRequired(){
      let vm = this;
      for(let i=0; i<this.row.length; i++){
        for(let [key, attr] of Object.entries(this.row[i])){
          if(attr.required == true){
            if(attr.type == 'number'){
              if(isNaN(attr.value)){
                let msg = getTitleByProps(key);
                this.row[i]._error = true;
                this.$forceUpdate();
                return '第'+ (i * 1 + 1) + '行，' + msg + '列 不能为空！'
              }
            }else{
              if((attr.value+'').trim() == ''){
                let msg = getTitleByProps(key);
                this.row[i]._error = true;
                this.$forceUpdate();
                return '第'+ (i * 1 + 1) + '行，' + msg + '列 不能为空！'
              }
            }
          }
        };
      };
      return true;
      function getTitleByProps(props){
        for(let i=0; i<vm.col.length; i++){
          if(vm.col[i].props == props){
            return vm.col[i].title;
          }
        }
      };
    },
    
    //拖拽列宽
    dragDown(colItem, event){
      let vm = this;
      window.addEventListener('mousemove', mousemove);
      window.addEventListener('mouseup', mouseup);
      let left = colItem._left + colItem.width - this.geScrollLeftArea;
      this.dragLeft = left;
      this.dragLineShow = true;
      let startx = event.clientX;
      function mousemove(e){
        let dis = e.clientX - startx;
        vm._willWidth = colItem.width + dis;
        vm.dragLeft = left + dis;
      };
      function mouseup(){
        if(vm._willWidth < 60){
          vm._willWidth = 60;
        };
        for(let i=0; i<vm.col.length; i++){
          if(vm.col[i]._colid == colItem._colid){
            vm.col[i].width = vm._willWidth;
            //需要重新计算绝对距离，因为排版根据left！
            for (let j = 0; j < vm.col.length; j++) {
              let left = 0;
              if(vm.showCheckBox){
                left = 40;
              };
              for (let k = 0; k < j; k++) {
                left += vm.col[k].width;
              }
              vm.col[j]._left = left;
            }
            break ;
          }
        };
        vm.dragLineShow = false;
        window.removeEventListener('mousemove', mousemove);
        window.removeEventListener('mouseup', mouseup);
      };
    },
    //拷贝对象
    jsonClone(obj){
      return JSON.parse(JSON.stringify(obj))
    },
    /**
    * @name 深拷贝
    */
    deepClone(data) {
      let t = type(data), o, i, ni;
      if(t === 'array') {
        o = [];
      }else if( t === 'object') {
        o = {};
      }else {
        return data;
      }
      if(t === 'array') {
        for (i = 0, ni = data.length; i < ni; i++) {
          o.push(this.deepClone(data[i]));
        }
        return o;
      }else if( t === 'object') {
        for( i in data) {
          o[i] = this.deepClone(data[i]);
        }
        return o;
      }
      function type(obj) {
        let toString = Object.prototype.toString;
        let map = {
          '[object Boolean]'  : 'boolean', 
          '[object Number]'   : 'number', 
          '[object String]'   : 'string', 
          '[object Function]' : 'function', 
          '[object Array]'    : 'array', 
          '[object Date]'     : 'date', 
          '[object RegExp]'   : 'regExp', 
          '[object Undefined]': 'undefined',
          '[object Null]'     : 'null', 
          '[object Object]'   : 'object'
        };
        return map[toString.call(obj)];
      }
    },

  },

  beforeDestroy(){
    document.removeEventListener("click", this.hideOption);
    window.removeEventListener("resize", this.fixedWidthHeight);
  },

  destroyed() {
  },
};
</script>

<style scoped>
* {
  box-sizing: border-box;
}
.nowrap {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
