  position: relative;
  width: 100%;
  height: 100%;
}
.container {
  position: relative;
  border: 1px solid #d8d8d8;
  background: #fff;
  font-size: 12px;
  color: #828282;
  overflow: hidden;
  -webkit-font-smoothing: antialiased;
}

/* 滚动条 */
.container:hover .scrollbar-x-drag {
  height: 10px;
  opacity: 0.8;
}
.container:hover .scrollbar-y-drag {
  width: 10px;
  opacity: 0.8;
}
.scrollbar-x,
.scrollbar-y {
  position: absolute;
  background: transparent;
}
.scrollbar-x {
  height: 2px;
  bottom: 0px;
  left: 0px;
}
.scrollbar-y {
  width: 2px;
  top: 0px;
  right: 0px;
}
.scrollbar-x-drag,
.scrollbar-y-drag {
  position: absolute;
  background: #6b6b6b;
  border-radius: 5px;
  opacity: 0.4;
  z-index: 100;
}
.scrollbar-x-drag {
  height: 5px;
  min-width: 30px;
  bottom: 1px;
  transition: height 0.4s, opacity 0.4s;
}
.scrollbar-y-drag {
  width: 5px;
  min-height: 30px;
  right: 1px;
  transition: width 0.4s, opacity 0.4s;
}

/* 容器 */
.body {
  position: relative;
  height: 100%;
  overflow: hidden;
}
.expand {
  position: absolute;
  left: 0px;
  top: 0px;
  visibility: hidden;
}
.tbl-cell {
  position: absolute;
  background: transparent;
  border-top: 1px solid #d8d8d8;
  border-left: 1px solid #d8d8d8;
  padding: 0px;
}
.tbl-cell input{
  background: transparent;
}
.check-cell{
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px !important;
}
.check-cell.modify{
  box-shadow: 3px 0px 1px #6eb7ff inset;
}
.check-cell.modify span{
  color: #2988e7 !important;
}
.check-cell.error{
  box-shadow: 3px 0px 1px #ff4f43 inset;
}
.check-cell.error span{
  color: #ff4f43 !important;
  font-weight: bold;
  font-size: 13px !important;
}
.check-cell .fa-check-square{
  cursor: pointer;
  color: dodgerblue;
}
.check-cell .fa-square{
  cursor: pointer;
  color: dodgerblue;
  position: relative;
}
.check-cell .fa-square:after{
  content: ' ';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  background: #fff;
  width: 9px;
  height: 3px;
  border-radius: 1px;
}
.check-cell .fa-square-o{
  cursor: pointer;
  color: silver;
}
/* 表头 */
.sheet-header,
.sheet-header-left,
.sheet-header-right {
  position: absolute;
  top: 0px;
}
.sheet-header .icon{
  font-size: 14px;
  display: inline-block;
  width: 15px;
  margin-left: 12px;
}
.sheet-header-left {
  z-index: 11;
}
.sheet-header-right {
  z-index: 10;
}
/* 边框border修正 */
.sheet-header .tbl-cell {
  line-height: 39px;
  font-weight: normal;
  color: #828282;
  font-size: 14px;
  border-bottom: 1px solid #d8d8d8;
}
.sheet-header-left .tbl-cell:nth-of-type(1){
  border-left-width: 0px;
}
.sheet-header-left .tbl-cell:nth-last-of-type(1) {
  border-right: 1px solid #d8d8d8;
}
.sheet-body-left .tbl-cell {
  border: none;
  border-top: 1px solid #d8d8d8;
  border-right: 1px solid #d8d8d8;
}

/* 表体 */
.sheet-body,
.sheet-line,
.sheet-body-left,
.sheet-body-right {
  position: absolute;
}
.sheet-body{
  width: 100%;
}
.sheet-line{
  width: 100%;
  height: 40px;
  background: #fff;
}
.sheet-line .sheet-body-left .tbl-cell,
.sheet-line{
  transition: background .25s;
}
.sheet-body-left .tbl-cell{
  background: #fff;
}
.sheet-header .tbl-cell{
  background: #fff;
}
.sheet-line:hover {
  background: #f2f3ff;
}
.sheet-line:hover .sheet-body-left .tbl-cell{
  background: #f2f3ff;
}
.sheet-body-left {
  z-index: 11;
}
.sheet-body-body {
  z-index: 10;
}
.sheet-body .tbl-cell{
  color: #000;
}
.sheet-body-left .tbl-cell:nth-last-of-type(1) {
  box-shadow: 1px 4px 8px rgba(0, 0, 0, 0.2);
}
/* 其他 */
.sheet-header-right .tbl-cell,
.sheet-header-left .tbl-cell {
  border-bottom-width: 1px;
}
.sheet-body-right,
.sheet-header-right {
  will-change: transform;
  transition-timing-function: cubic-bezier(0.1, 0.57, 0.1, 1);
  transition-duration: 0ms;
}

.cell {
  height: 40px;
  width: 100%;
  position: relative;
}
.cell:hover .edit-close{
  display: block;
}
.cell .edit-close{
  display: none;
  position: absolute;
  right: 4px;
  top: 50%;
  transform: translateY(-50%);
  color: #bebebe;
  font-size: 14px;
  cursor: pointer;
}
.cell .edit-close:hover{
  color: #9e9e9e;
}
.text {
  height: 40px;
  width: 100%;
  line-height: 40px;
  text-indent: 10px;
}
/* 输入框 */
.edit-inp {
  display: block;
  padding-right: 4px;
  width: 100%;
  height: 39px;
  line-height: 39px;
  border: 1px solid transparent;
  outline: none;
  text-indent: 10px;
}
.edit-inp:focus {
  border: 1px solid rgb(247, 132, 50);
}
/* option、提示框的过度 */
.option-enter-active {
  transition: all .24s ease;
  transform-origin: center top;
}
.option-leave-active{
  transition: none;
}
.option-enter, 
.option-leave-to {
  transform: scaleY(0);
  opacity: 0;
}

/* placeholder的颜色 */
input:-moz-placeholder{
    color: #c6c6c6;
    opacity: 1; 
}
input::-moz-placeholder{
    color: #c6c6c6; 
    opacity: 1; 
}
input:-ms-input-placeholder{
    color: #c6c6c6; 
    opacity: 1; 
}
input::-webkit-input-placeholder{
    color: #c6c6c6; 
    opacity: 1; 
}
input::input-placeholder {
    color: #c6c6c6; 
    opacity: 1; 
}
.option{
  position: absolute;
  background: #fff;
  border: 1px solid rgb(211, 211, 211);
  z-index: 2018;
  padding-bottom: 8px;
  padding-top: 8px;
  color: #878787;
  font-size: 13px;
  box-shadow: 0px 2px 6px  rgba(6, 6, 6, 0.3);
}
.option .opp{
  position: relative;
  max-height: 200px;
  overflow-y: auto;
  overflow-x: hidden;
}
.option .arrow{
  position: absolute;
  left: 16px;
  top: -6px;
  transform: rotate(45deg);
  content: ' ';
  width: 10px;
  height: 10px;
  z-index: 2019;
  background: #fff;
  border-left: 1px solid rgb(211, 211, 211);
  border-top: 1px solid rgb(211, 211, 211);
}

.option-item{
  padding: 9px 10px;
  cursor: pointer;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.option-item:hover{
  background: #efefef;
  overflow: inherit;
  white-space: inherit;
  text-overflow: inherit;
}
.option-item.select{
  background: #70b5ff;
  color: #fff;
}
.option-item.select:hover{
  background: #70b5ff;
}

/* drag功能的样式 */
.drag-pad{
  position: absolute;
  width: 10px;
  left: calc(100% - 5px);
  z-index: 101;
  background: transparent;
  cursor: col-resize;
}
.drag_line{
  position: absolute;
  height: 100%;
  z-index: 108;
  width: 1px;
  background: #a8a8a8;
  top: 0px;
}
.hover-show{
  position: absolute;
  z-index: 2017;
  background: rgb(250, 250, 250);
  border: 1px solid #dadada;
  padding: 10px;
  font-size: 12px;
  color: rgb(65, 183, 238);
  box-shadow: 0px 1px 10px rgba(182, 182, 182, 0.5);
}
.hover-show:before{
  content: ' ';
  position: absolute;
  top: calc(100% - 5px);
  left: 50%;
  z-index: 2017;
  width: 10px;
  height: 10px;
  transform: translateX(-50%) rotate(45deg);
  background: rgb(250, 250, 250);
  border-bottom: 1px solid #dadada;
  border-right: 1px solid #dadada;
}
</style>



